      ],
      currentSlide: 0, // 현재 슬라이드 인덱스
    };
  },
  methods: {
    nextSlide() {
      this.currentSlide = (this.currentSlide + 1) % this.slides.length;
    },
    goToSlide(index) {
      this.currentSlide = index;
    },
    navigateToProduct() {
      this.$router.push('/product'); // /product 페이지로 이동
    },
  },
};
